import { withStyles } from "@material-ui/core/styles";
import IconButton from "@mui/material/IconButton";
import Dialog from "@mui/material/Dialog";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import React from "react";
import CloseIcon from "@mui/icons-material/Close";
import { Box } from "@mui/material";
import { Grid } from "@mui/material";
import { Typography } from "@material-ui/core";
import { styled } from "@mui/material/styles";

const styles = () => ({
	box: {
		padding: "30px",
	},
});

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
	"& .MuiDialogContent-root": {
		padding: theme.spacing(2),
	},
	"& .MuiDialogActions-root": {
		padding: theme.spacing(1),
	},
}));

const BootstrapDialogTitle = (props) => {
	const { children, onClose, ...other } = props;

	return (
		<DialogTitle sx={{ m: 0, p: 2 }} {...other}>
			{children}
			{onClose ? (
				<IconButton
					aria-label="close"
					onClick={onClose}
					sx={{
						position: "absolute",
						right: 8,
						top: 8,
						color: (theme) => theme.palette.grey[500],
					}}
				>
					<CloseIcon />
				</IconButton>
			) : null}
		</DialogTitle>
	);
};

const InfoModal = (props) => {
	const { classes, openInfoModal, onCloseInfoModal } = props;

	const [playerName, setPlayerName] = React.useState("");

	const handleChangePlayerName = (event) => {
		setPlayerName(event.target.value);
	};
	const handleCloseCancel = () => {
		onCloseInfoModal();
		setPlayerName("");
	};
	const handleClose = () => {
		onCloseInfoModal(playerName);
		setPlayerName("");
	};

	return (
		<BootstrapDialog
			onClose={handleClose}
			aria-labelledby="customized-dialog-title"
			open={openInfoModal}
		>
			<BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
				Qu'est-ce que l'Ascenseur?
			</BootstrapDialogTitle>
			<DialogContent dividers>
				<Typography gutterBottom paragraph>
					Se joue de 4  6 joueurs avec un jeu de cinquante-deux cartes selon les
					spcifications suivantes :  cinq joueurs, deux 2 sont retirs ;  six joueurs
					on retire tous les 2.
				</Typography>
				<Typography gutterBottom paragraph>
					Le jeu consiste en une srie de manches se jouant avec un nombre de cartes en
					main d'abord croissant puis ensuite dcroissant. Le premier tour se joue  une
					carte, le second  deux cartes, le troisime  trois cartes, et ainsi de suite,
					jusqu' ce qu' une manche o toutes les cartes sont distribues.  partir du
					tour suivant, on passe au mode de distribution dcroissante.
				</Typography>
				<Typography gutterBottom paragraph>
					Chaque manche consiste en 4 phases: la distribution des cartes, l'annonce du
					contrat, la joute, le dcompte.
				</Typography>
				<Typography gutterBottom paragraph>
					<strong>1. La distribution des cartes:</strong>
					Le donneur distribue <em>n</em> cartes (varie en fonction du numro de manche
					auquel on est rendu)  chaque joueur. Il place le talon au centre, retourne la
					carte du dessus face visible qui dsignera la couleur de l'atout.
				</Typography>
				<Typography gutterBottom paragraph>
					<strong>2. L'annonce du contrat:</strong> En commenant par le jouer suivant le
					donneur, chaque joueur annonce le nombre de leves qu'il pense remporter durant
					cette manche. Le nombre annonc par chacun ne doit pas dpasser le nombre de
					leves  remporter durant la manche (c'est--dire le nombre de cartes que chaque
					joueur a en main) et la somme des nombres annoncs par chacun ne doit pas
					totaliser exactement le nombre de leves (sinon il y aurait possibilit que tous
					les joueurs soient gagnants et le jeu ne souhaite pas cel).
				</Typography>
			</DialogContent>
		</BootstrapDialog>
	);
};

export default withStyles(styles, { name: "InfoModal" })(InfoModal);
